@import url("https://fonts.googleapis.com/css?family=Libre+Baskerville|Roboto+Slab");
body,
ul,
li,
h1,
h2,
h3 {
  list-style-type: none;
  margin: 0;
  padding: 0; }

body {
  font-size: 1rem;
  font-family: "Roboto Slab", serif; }

a {
  color: #416967;
  text-decoration: none; }

h1 {
  font-family: "Libre Baskerville", serif;
  font-size: 7rem;
  padding: 12rem;
  letter-spacing: 0.5rem;
  margin: 0 auto; }
  @media (max-width: 1199px) {
    h1 {
      padding: 7rem;
      background: none; } }

nav {
  font-size: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  position: relative; }
  nav li {
    display: inline-block; }
    nav li a {
      display: block;
      padding: 2rem 1rem;
      font-size: 1.5rem; }
  @media (max-width: 1199px) {
    nav .main-menu__items {
      position: absolute;
      top: 100%;
      right: -20px;
      background-color: #fff;
      display: inline-block;
      width: 90%;
      max-width: 400px;
      height: 100vh;
      transform: translateX(110vw);
      transition: transform .5s;
      z-index: 50;
      padding: 20px; }
    nav .main-menu__mobile {
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-flex: 1;
      -ms-flex-positive: 1;
      flex-grow: 1;
      -webkit-box-pack: end;
      -ms-flex-pack: end;
      justify-content: flex-end; }
    nav .main-menu__mobile-toggle {
      font-size: 0;
      display: inline-block;
      width: 36px;
      height: 36px;
      background-image: url("../static/images/icons/menu.svg");
      background-size: cover;
      background-position: left top;
      margin-left: 15px;
      -webkit-transition: background-position .3s;
      transition: background-position .3s; } }

.row {
  margin: 0 auto; }
  .row.wide {
    max-width: 1680px; }

.wide {
  padding: 0 20px !important; }

button {
  background: none;
  border: 0;
  padding: 5px; }
  button:focus {
    outline: 0; }

.active {
  background: silver; }

header {
  background: #b3d0cf;
  text-align: center; }

.logo {
  color: #416967;
  font-size: 1.5rem;
  font-family: "Libre Baskerville", serif;
  text-decoration: none;
  font-weight: bold;
  padding: 2rem 2rem;
  display: block; }

.filter-holder li {
  display: inline-block; }

.filter-holder button {
  margin: 0 5px 5px 0;
  padding: 11px 22px 10px;
  border: 2px solid #416967;
  font-family: "Roboto Slab", serif;
  transition: color .3s, background .3s, opacity .2s; }
  .filter-holder button:hover {
    background: #ddebea; }
  .filter-holder button.clear-filters {
    font-size: 0;
    width: 42px;
    height: 42px;
    position: relative;
    padding: 0; }
    .filter-holder button.clear-filters:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      height: 38px;
      width: 38px;
      background-image: url("../static/images/icons/icon-close-thin.svg");
      opacity: .4; }

.filter-holder section {
  margin: 10px 0;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 30px; }

.span-col-4 {
  grid-column: span 4 / auto; }

.span-col-3 {
  grid-column: span 3 / auto; }

.items {
  display: grid;
  grid-gap: 40px;
  margin-top: 3rem; }
  @media (min-width: 768px) {
    .items {
      grid-template-columns: repeat(2, 1fr); } }
  @media (min-width: 992px) {
    .items {
      grid-template-columns: repeat(3, 1fr); } }
  @media (min-width: 1200px) {
    .items {
      grid-template-columns: repeat(4, 1fr); } }
  .items article {
    position: relative; }
  .items .image-holder img {
    width: 100%; }
  .items .details {
    padding: 12px 0 25px; }
  .items .link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .items h2 {
    line-height: 32px; }
  .items h3 {
    line-height: 32px;
    text-transform: uppercase;
    font-weight: normal;
    font-size: 16px;
    letter-spacing: 0.1rem;
    color: #999999; }
    .items h3 time {
      margin: 0 0 0 2rem; }
  .items .tags {
    margin: 10px 0 0 -8px; }
    .items .tags .tag {
      padding: 5px 9px 6px;
      background: #eeeeee;
      margin: 0 3px 0 0;
      font-size: 0.8rem; }

.intro-home {
  background: url(../static/images/home-1140.jpg) no-repeat;
  background-size: cover;
  text-align: center; }
  .intro-home h1 {
    font-size: 2rem;
    color: #eeeeee;
    letter-spacing: 0;
    font-weight: normal; }
    .intro-home h1 span {
      display: block;
      font-size: 1rem; }
      .intro-home h1 span:before {
        display: block;
        content: '';
        margin: 20px auto;
        width: 50px;
        height: 2px;
        background-color: #eeeeee; }

.copy {
  max-width: 780px;
  margin: 0 auto; }
  .copy h2 {
    margin: 4rem 0 2rem; }

footer {
  background: #416967;
  color: #eeeeee;
  padding: 3rem;
  text-align: center; }
  footer a {
    color: #eeeeee; }
  footer li {
    display: inline;
    padding: 0 2rem; }
